export default {
	data() {
		return {
			refreshHeight: 60, // 刷新组件的高度
			contentHeight: 0,
			containerHeight: 0,
			scrollTop: 60,
			currentTop: 0,
			topStateOffset: 10, // 离顶部 30算触发下拉刷新
			bottomStateOffset: 30 // 离底部 30算触发下拉刷新
		}
	},
	mounted() {
		// 挂载成功后获取一下自身的高度
		const containerQuery = uni.createSelectorQuery().in(this)
		containerQuery.select('.refresh_view').boundingClientRect(data => {
			this.containerHeight = data.height
			console.log(data.height)
		}).exec()

		const contentQuery = uni.createSelectorQuery().in(this)
		contentQuery.select('.refresh_content').boundingClientRect(data => {
			this.contentHeight = data.height
			console.log(data.height)
		}).exec()
	},
	methods: {
		scrollHandler(e) {
			// console.log(e.detail.scrollTop)
			// this.currentTop = e.detail.scrollTop
			// if(this.scrollTop <= this.topStatusOffset ){
			// 	console.log('触发刷新')
			// }
		},
		lowerHandler(e) {
			// 每次快触发到底部时，获取一次content的高度，以便判断是否上拉刷新
			const contentQuery = uni.createSelectorQuery().in(this)
			contentQuery.select('.refresh_content').boundingClientRect(data => {
				this.contentHeight = data.height
				console.log(data.height)
			}).exec()
		},
		upperHandler(e) {

		},
		touchEnd() {
			// 手离开
		}
	}
}
